<template>
    <div id="main" class="v2">
        <div class="rowBlock1">
            <span>
                支援陀螺儀：
            </span>
            <span :class="isSupportDeviceMotionCSS()">
                {{isSupportDeviceMotionLabel()}}
            </span>
        </div>
        <div class="rowBlock2">
            <span>
                支援震動：
            </span>
            <span :class="isSupportVibrateCSS()">
                {{isSupportVibrateLabel()}}
            </span>
        </div>
        <div class="rowBlock3">
            <div class="demoBox" :class='demoBoxBGJudge()'>
            </div>
            <span>請先點擊屏幕一下</span>
        </div>
        
    </div>
</template>

<script>
export default {
    created(){
        this.addShakeEvent();
        if("vibrate" in navigator)
          this.isSupportVibrate=true;
    },
    data(){
        return{
          isSupportDeviceMotion:false,
          isSupportVibrate:false,
          boxColorIndex:0,
          speed:15,  // 用来判定的加速度阈值，太大了则很难触发
          x:0,
          y:0,
          z:0,
          lastX:0,
          lastY:0,
          lastZ:0,
        }
    },
    methods:{
        demoBoxBGJudge()
        {
          if(this.boxColorIndex==1)
            return {"_1":true};
          else if(this.boxColorIndex==2)
            return {"_2":true};
          else if(this.boxColorIndex==3)
            return {"_3":true};
          else if(this.boxColorIndex==4)
            return {"_4":true};
          else if(this.boxColorIndex==5)
            return {"_5":true};
        },
        isSupportDeviceMotionLabel(){
            if(this.isSupportDeviceMotion)
                return "支援";
            else
                return "不支援";
        },
        isSupportDeviceMotionCSS(){
            if(this.isSupportDeviceMotion)
                return {"green":true};
            else
                return {"red":true};
        },
        isSupportVibrateCSS(){
            if(this.isSupportVibrate)
                return {"green":true};
            else
                return {"red":true};
        },
        isSupportVibrateLabel(){
            if(this.isSupportVibrate)
                return "支援";
            else
                return "不支援";
        },
        addShakeEvent(){
            if(window.DeviceMotionEvent) {
              window.addEventListener('devicemotion',this.shakeEvent, false);
            }
        },
        shakeEvent(){
            this.isSupportDeviceMotion=true;
            let acceleration = event.accelerationIncludingGravity;
            this.x = acceleration.x;
            this.y = acceleration.y;
            if(Math.abs(this.x-this.lastX) > this.speed || Math.abs(this.y-this.lastY) > this.speed) {
                // 用户设备摇动了，触发响应操作
                // 此处的判断依据是用户设备的加速度大于我们设置的阈值
                this.boxColorIndex++;
                if(this.boxColorIndex==6)
                  this.boxColorIndex=1;
                if("vibrate" in navigator){
                  navigator.vibrate(700);
                }
            }
            this.lastX = this.x;
            this.lastY = this.y;
        },
    }
}
</script>

<style lang="scss" scoped>
.red{
    color:red;
}
.green{
    color:green;
}
._1
{
  background-color:gold !important;
}
._2{
  background-color:firebrick !important;
}
._3{
  background-color:deepskyblue !important;
}
._4{
  background-color:greenyellow !important;
}
._5{
  background-color:hotpink !important;
}
#main{
    width:100%;
    .rowBlock1{
      width:100%;
      position: absolute;
      top: 20%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    .rowBlock2{
      width:100%;
      position: absolute;
      top: 40%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    .rowBlock3{
      width:100%;
      position: absolute;
      top: 70%;
      left: 50%;
      transform: translate(-50%,-50%);
      .demoBox{
        width: 100px;
        height:100px;
        margin: 10px auto;
        background-color:gray;
      }
    }
}
</style>





